.parentHardware {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-column-gap: 0px;
    grid-row-gap: 0px;
    width: 100%;
    height: 100vh;
}
    
.divHardware0 {
    grid-area: 1 / 2 / 1 / 1;
     overflow: hidden;
}
.divHardware1 {
    grid-area: 2 / 1 / 2 / 1;
     overflow: hidden;
}
.divHardware2 {
    grid-area: 1 / 2 / 2 / 2;
     overflow: hidden;
}
.divHardware3 {
    grid-area: 2 / 2 / 2 / 2;
     overflow: hidden;
}
.divHardware4 {
    grid-area: 2 / 3 / 2 / 3;
     overflow: hidden;
}
.divHardware5 {
    grid-area: 1 / 3 / 1 / 3;
     overflow: hidden;
}

.divHardware0:hover,.divHardware1:hover,.divHardware2:hover,.divHardware3:hover,.divHardware4:hover,.divHardware5:hover{
    cursor: pointer;
}

